a {
  border: none;
  text-decoration: none !important;
}
a.nope,
a.nope:hover {
  color: inherit;
}
a.disabled {
  pointer-events: none;
  color: inherit;
}
a.referenced {
  text-decoration: none;
  border-bottom: 1px dashed currentColor;
}
article,
aside {
  display: table;
  padding: 4px 10px;
}
aside {
  margin: 1em;
}
section > aside {
  margin: 2px;
}
aside.compact {
  margin: 0 1em;
}
aside a {
  position: relative;
  z-index: 15;
}
a,
aside,
.bfloat,
.soundcloud,
.watch,
.control {
  cursor: pointer;
}
blockquote {
  word-break: break-all;
}
.control {
  margin-left: 1em;
  display: inline-block;
  float: right;
}
#sync,
#onlineCount {
  cursor: auto;
}
article {
  margin: 2px;
}
article header {
  padding-left: 14px;
}
article .hat {
  width: 35px;
  height: 70px;
  left: 10px;
  top: -28px;
  background-size: 35px 70px;
  pointer-events: none;
}
article small {
  padding-top: 0.5em;
}
small {
  clear: left;
  display: block;
}
small:empty {
  display: none;
}
section > small {
  padding-bottom: 0.5em;
}
.close {
  font-weight: bold;
}
.embed {
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 4px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  display: inline-block;
  font-style: normal;
  margin: -2px;
  padding: 2px;
  color: black;
}
del {
  text-decoration: none;
}
del,
del em {
  background-color: black;
  color: black;
}
del:hover,
del.reveal,
del:hover em,
del.reveal em {
  color: white;
}
em {
  font-style: normal;
}
fieldset {
  border: 0;
}
figcaption {
  display: block;
  margin: 2px 0;
  text-decoration: none;
}
figcaption *:before {
  content: ' ';
}
.imageSearch {
  font-weight: bold;
  display: none;
}
figure {
  display: inline;
  margin: 0;
}
figure img {
  border: 0;
  clear: both;
  float: left;
  margin: 3px 20px 0 0;
  vertical-align: top;
}
figure img.noMargin {
  margin-right: 0;
}
section figure img {
  margin-bottom: 0.5em;
}
#headerTopMargin {
  width: 100%;
  height: 1.1em;
}
h1 {
  margin-top: 0;
}
body > h1 {
  margin-bottom: 0;
}
threads > h1 {
  margin-bottom: 0.5em;
}
h1,
h2 {
  text-align: center;
}
h3 {
  display: inline;
  font-size: inherit;
}
.retardedBrowser {
  font-size: 18px;
  text-align: right;
}
.retardedBrowser img {
  height: 18px;
  width: 18px;
  position: relative;
  top: 2px;
}
#hiddenUpload,
#importSettings {
  display: none;
}
#hover_overlay {
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  position: fixed;
  pointer-events: none;
  display: flex;
  z-index: 300;
}
#hover_overlay > video,
#hover_overlay > img {
  max-height: 100%;
  max-width: 100%;
  margin: auto auto;
  display: block;
  object-fit: contain;
}
#loadingImage {
  position: fixed;
  z-index: 310;
  right: 0;
  bottom: 0;
}
header {
  margin-left: -14px;
  padding-left: 14px;
  position: relative;
  display: inline-block;
}
header > *:before {
  content: ' ';
}
header code {
  font-family: inherit;
  font-weight: normal;
}
header .expansionLinks {
  margin-left: 1em;
}
hr {
  border: none;
  clear: both;
}
label.ident {
  display: inline-block;
  text-align: right;
  width: 3em;
}
section {
  display: block;
  margin: 0 1em 1em 1em;
}
section nav,
section p,
.preview nav {
  display: inline;
}
span.control .popup-menu {
  z-index: 10;
}
.popup-menu {
  padding: 5px;
  position: absolute;
  margin-top: 0.1em;
}
.popup-menu li {
  list-style: none;
  margin: 0;
  padding: 3px 3px 3px 5px;
}
.popup-menu,
.act {
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.act:before {
  content: "[";
}
.act:after {
  content: "]";
}
.postCheckbox {
  display: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  background: url('ui/box2x.png');
  width: 0.9em;
  height: 0.9em;
  background-size: 100% 100%;
  margin: 0;
}
.postCheckbox:checked {
  background: url('ui/sel2x.png'), url('ui/box2x.png');
  background-size: 100% 100%;
}
.postCheckbox:focus {
  outline: 0;
}
#banner {
  display: inline-block;
  text-align: center;
  position: fixed;
  width: 100%;
  margin-left: -8px;
  margin-right: -8px;
  top: 0;
  z-index: 100;
}
#banner_center .mobile {
  display: none;
}
#banner_center .mobile:-webkit-full-screen,
#banner_center .mobile:-moz-full-screen {
  display: block;
}
.bfloat {
  font-weight: bold;
  float: right;
  margin: 0 0.15em 0 0.15em;
}
.bfloat svg {
  width: 1em;
  height: 1em;
}
#options {
  margin-right: 0.2em;
}
#navTop {
  float: left;
  margin-left: 8px;
}
#catalog {
  margin: 0 0.85em;
}
#catalog article {
  width: 165px;
  max-height: 320px;
  display: inline-block;
  text-align: center;
  word-wrap: break-word;
  overflow: hidden;
  vertical-align: top;
  padding: 0.6em;
  margin: 0.15em;
}
#catalog small {
  clear: none;
  display: inline-block;
}
#catalog small span:first-child {
  margin-right: 0.2em;
}
.option_tab_sel {
  display: inline;
  -webkit-padding-start: 0px;
  -moz-padding-start: 0px;
}
.option_tab_sel li {
  list-style-type: none;
  float: left;
}
.option_tab_sel a {
  display: inline-block;
  padding: 7px;
  border-bottom: 1px solid;
}
.option_tab_cont {
  padding-top: 10px;
  padding-left: 5px;
  margin-bottom: 0px;
}
.option_tab_cont li {
  display: none;
  float: left;
  padding-top: 5px;
  width: 100%;
}
.option_tab_cont li label {
  padding-left: 0.2em;
}
.option_tab_cont li.tab_sel {
  display: inline-block;
}
#hidden {
  float: right;
}
input[type=number] {
  width: 3em;
}
#banner,
.bmodal,
.modal {
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0);
}
.notification {
  position: fixed;
  text-align: center;
  font-size: 120%;
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  width: 90%;
}
.notification.modal {
  overflow: auto;
}
#FAQ > ul,
#schedule > table {
  list-style-type: none;
  -webkit-margin-before: 0em;
  margin-top: 0em;
  -webkit-margin-after: 0em;
  margin-bottom: 0em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
  -webkit-padding-start: 0px;
  -moz-margin-start: 0em;
  -moz-margin-end: 0px;
  -moz-padding-start: 0px;
}
#UTCClock {
  text-align: center;
}
#UTCClock b {
  cursor: pointer;
  display: block;
}
#user_bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -100;
}
#user_bg video {
  height: auto;
  width: 100%;
}
.email {
  text-decoration: underline !important;
}
figure {
  display: block;
  position: relative;
}
.hat {
  background: url('ui/hat.png') no-repeat;
  position: absolute;
  left: 10px;
  top: -75px;
  width: 70px;
  height: 141px;
  z-index: 10;
}
#lock {
  background: url('ui/lock.svg') no-repeat left;
  background-size: 0.9em 0.9em;
  color: gray;
  float: right;
  padding-left: 15px;
}
.locked aside {
  display: none !important;
}
.omit {
  line-height: 200%;
}
.modal {
  display: none;
  text-align: left;
  position: fixed;
  max-height: 80%;
  max-width: 80%;
  overflow: auto;
  padding: 1em;
  white-space: nowrap;
  z-index: 200;
}
.bmodal {
  margin-right: 0.2em;
  right: 0;
}
.modal,
.bmodal,
.preview,
.popup-menu,
#banner {
  -webkit-box-shadow: 3px 3px 3px 0 rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 3px 3px 3px 0 rgba(0, 0, 0, 0.5);
  box-shadow: 3px 3px 3px 0 rgba(0, 0, 0, 0.5);
}
.pagination {
  display: inline-block;
  padding: 2px 10px;
  margin: 1px 0;
}
.pagination a,
.pagination strong {
  display: inline-block;
  padding: 4px;
}
.preview {
  padding: 4px 10px;
  position: absolute;
  z-index: 30;
}
.preview .control {
  display: none;
}
.themed {
  background-color: transparent;
  color: #444;
  outline: none;
}
#bottom {
  margin-bottom: 7px;
  min-width: 100px;
}
#bottom:focus {
  outline: 0;
}
#report-panel {
  position: fixed;
  right: 10px;
  bottom: 10px;
}
#report-panel #captcha,
#report-panel .message {
  margin: 1em 0;
}
#report-panel .error {
  color: red;
}
#report-panel .close {
  float: right;
  font-weight: bold;
  padding-left: 1em;
}
#options-panel .tab-4 input {
  border: none;
  width: 2em;
}
pre {
  height: auto;
  margin: 0;
  position: absolute;
  top: 0;
  width: auto;
  visibility: hidden;
}
pre,
.themed {
  border: 0;
  padding: 0;
}
#toggle {
  background: url('ui/pane.png') transparent;
  background-size: 78px 19px;
  border: 0;
  height: 19px;
  width: 78px;
}
#input {
  display: inline;
  margin: 0 0 -3px 0;
  overflow-y: hidden;
  resize: none;
  text-wrap: none;
  vertical-align: baseline;
}
#noscriptHeader {
  color: #b5bd68;
  margin-top: 1em;
}
@media screen and (max-device-width: 700px) {
  article {
    clear: both;
  }
  body {
    margin: 1px 0;
    -webkit-text-size-adjust: none;
  }
  blockquote {
    margin: 0.5em 0 0.5em 0.5em;
  }
  section {
    margin: 0;
  }
  #return {
    min-width: inherit;
    margin-right: 0.5em;
  }
  #banner {
    margin-left: 0;
    margin-right: 0;
  }
  #navTop {
    margin-left: 0;
  }
  #options {
    margin-right: 0;
  }
}
@media screen and (max-device-width: 700px) and (orientation: portrait) {
  section > blockquote,
  section > header,
  section > .omit {
    margin-left: 0.5em;
    margin-right: 0.5em;
  }
  section > header {
    clear: both;
  }
  .full figcaption i {
    display: block;
  }
}
@font-face {
  font-family: 'Icons';
  src: url('fonts/open-iconic.eot');
  src: url('fonts/open-iconic.eot?#iconic-sm') format('embedded-opentype'), url('fonts/open-iconic.woff') format('woff'), url('fonts/open-iconic.ttf') format('truetype'), url('fonts/open-iconic.otf') format('opentype'), url('fonts/open-iconic.svg#iconic-sm') format('svg');
  font-weight: normal;
  font-style: normal;
}
.oi[data-glyph].oi-text-replace {
  font-size: 0;
  line-height: 0;
}
.oi[data-glyph].oi-text-replace:before {
  width: 1em;
  text-align: center;
}
.oi[data-glyph]:before {
  position: relative;
  top: 1px;
  font-family: 'Icons';
  display: inline-block;
  speak: none;
  line-height: 1;
  vertical-align: baseline;
  font-weight: normal;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.oi[data-glyph]:empty:before {
  width: 1em;
  text-align: center;
  box-sizing: content-box;
}
.oi[data-glyph].oi-align-left:before {
  text-align: left;
}
.oi[data-glyph].oi-align-right:before {
  text-align: right;
}
.oi[data-glyph].oi-align-center:before {
  text-align: center;
}
.oi[data-glyph].oi-flip-horizontal:before {
  -webkit-transform: scale(-1, 1);
  -ms-transform: scale(-1, 1);
  transform: scale(-1, 1);
}
.oi[data-glyph].oi-flip-vertical:before {
  -webkit-transform: scale(1, -1);
  -ms-transform: scale(-1, 1);
  transform: scale(1, -1);
}
.oi[data-glyph].oi-flip-horizontal-vertical:before {
  -webkit-transform: scale(-1, -1);
  -ms-transform: scale(-1, 1);
  transform: scale(-1, -1);
}
.oi[data-glyph=chevron-bottom]:before {
  content: '\e034';
}
